import { Component, ViewChild, ComponentFactoryResolver, Input, OnInit, SimpleChanges } from '@angular/core';

import { EventService } from '../../shared/service/event.service';

import { AppAnchorDirective } from './anchor.directive';
import { ComItem } from './com-item';

@Component({
    selector: 'app-coms',
    template: '<section>\n' +
        '<ng-template appAnchor></ng-template>\n' +
        '<div id="home-app-coms-sec" style="display:none;"></div></section>'
})
export class ComsComponent implements OnInit {

    @Input() coms: ComItem[];

    @ViewChild(AppAnchorDirective) appAnchor: AppAnchorDirective;

    constructor(private componentFactoryResolver: ComponentFactoryResolver, private eventService: EventService) {
    }

    ngOnInit() {
    }

    // tslint:disable-next-line:use-life-cycle-interface
    ngOnChanges(changes: SimpleChanges): void {
        // const viewContainerRef = this.appAnchor.viewContainerRef;
        // viewContainerRef.clear();
        this.loadComponent();
    }

    loadComponent() {
        const viewContainerRef = this.appAnchor.viewContainerRef;
        viewContainerRef.clear();
        this.coms.forEach((v) => {
            const componentFactory = this.componentFactoryResolver.resolveComponentFactory(v.component);
            const componentRef = viewContainerRef.createComponent(componentFactory);
            (componentRef.instance).tagData = v.tagData;
        });
    }
}